* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Karla, sans-serif;
  text-decoration: none;
  list-style: none;
}

header {
  padding: 2rem 0;
  position: fixed;
  top: 0;
  left: 0;
}

body {
  padding: 2px 50px 0;
}

.filter {
  justify-content: center;
  align-items: center;
  margin-top: 100px;
  display: flex;
}

select {
  height: 40px;
  color: #131b23;
  cursor: pointer;
  border: 1px solid #131b23;
  border-radius: 5px;
  margin-left: 10px;
  padding-left: 10px;
  font-size: 1em;
  font-weight: 250;
}

select option {
  font-size: 15px;
}

.products {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

.product {
  width: 200px;
  background: #ffffff31;
  border-radius: 10px;
  margin: 40px 20px 10px;
  padding: 28px;
  display: block;
}

.product__image {
  object-fit: contain;
  width: 100%;
  height: 100%;
  border-radius: 20px 20px 0 0;
  justify-content: center;
  display: flex;
}

.product__name {
  color: #131b23;
  color: #f64d33;
  font-size: 1.125em;
  font-weight: bold;
}

.product__price {
  color: #131b23;
  color: #fff;
  margin-top: 3px;
  margin-bottom: 10px;
  font-size: 1.125em;
  font-weight: bold;
  display: flex;
}

.product__category {
  color: #131b23;
  color: #fff;
  margin-bottom: 5px;
  font-size: .8em;
  font-weight: 300;
  display: flex;
}

.product__cart {
  color: #fff;
  text-align: center;
  cursor: pointer;
  width: 150px;
  height: 38px;
  background: #f64d33;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-weight: 200;
  box-shadow: 0 2px 4px #0003;
}

.product__cart:hover {
  background: #fa614a;
}

.loader {
  width: 60px;
  height: 60px;
  border: 16px solid #fff;
  border-top-color: #3498db;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
  animation: spin 2s linear infinite;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@media (max-width: 420px) {
  body {
    flex-direction: column;
    justify-content: center;
    padding: 0;
    display: flex;
  }

  .logo a {
    position: absolute;
    top: 10px;
    left: 20px;
  }

  select {
    margin-top: 20px;
    margin-left: 10px;
  }

  select option {
    font-size: 12px;
  }

  .products {
    flex-direction: column;
    align-items: center;
    margin-top: 15px;
    display: flex;
  }

  .product {
    width: 80%;
    border-radius: 20px;
    margin-bottom: 70px;
    display: block;
  }

  .product__image {
    width: 100%;
    object-fit: contain;
    border-radius: 20px 20px 0 0;
  }

  .product__cart {
    width: 250px;
  }
}

